.item {
  .quantity-wrapper {
    display: flex;

    & > * {
      width: 25px;
      height: 25px;
      text-align: center;
      border: solid 1px #d3d3d3;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .plus,
    .minus {
      cursor: pointer;
      transition: 0.3s ease;

      &:hover {
        background: #d3d3d3;
      }

      &[data-pending="true"] {
        cursor: not-allowed;
      }
    }

    .quantity {
      border-left: none;
      border-right: none;

      span {
        font-size: 12px;
      }
    }
  }

  .oos {
    font-size: 12px;
    width: 50%;
    margin: 0px;
    text-align: right;

    span {
      font-size: 12px;
      text-decoration: underline;
      cursor: pointer;
    }
  }
}
