
.form-field {
    width: 100%;
    display: flex; 
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 10px;
    color: var(--text-primary-color);
    
    label, 
    input {
        font-size: 14px;
    }
    
    label {
        font-weight: bold;
        color: var(--text-primary-color);
    }
    
    input, textarea, select {
        width: 100%;
        padding: 10px 10px 8px 15px;
        border: #eee; 
        border-radius: 2px;
        background: white;
        
        input { height: 36px; }
        textarea  { height: 100px; }
    }
    
    // Custom Checkbox Input
    .form-checkbox {
        position: relative;
        display: block;
        
        padding-left: 30px; 
        
        .checkbox {
            width: 20px; 
            height: 20px; 
            border-radius: 2px; 
            padding: 2.5px;

            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            inset: 0 auto auto 0;

            background: white;
            border: solid 1px var(--text-primary-color);
            transition: background .1s ease-in;

            .form-checkbox-icon {
                opacity: 0;
                width: 100%; 
                height: 100%; 
                object-fit: contain;
                color: white;
                transition: opacity .1s ease-in;
            }
        }

        .checkbox-label {
            margin: 0;
            text-align: left;
            font-size: 13px;
            font-weight: normal;
        }

        input { 
            appearance: none;
            width: 1px; 
            height: 1px; 
            padding: 0; 
            margin: 0;
            
            opacity: 0;
            position: absolute; 
            inset: 25px auto auto 10px;;
        }

        input:checked + .checkbox {
            background: var(--text-primary-color);
            .form-checkbox-icon { opacity: 1; }
        }
    }

    // Custom Select
    .form-select {
        width: 100%;
        position: relative; 
        isolation: isolate;
        
        select { z-index: -1; }
        
        svg { 
            width: 18px; 
            height: 18px;
            object-fit: contain;
            pointer-events: none;
            
            z-index: 1;
            position: absolute; 
            inset: 50% 12px auto auto;
            translate: 0 -50%;
            
            &, path { fill: var(--text-primary-color); }
        }
    }
}