@import '../../../scss/breakpoints.scss';

.open-sidecart {
  cursor: pointer;
}

#cart-drawer-container {
  position: fixed;
  top: 0px;
  right: 0px;
  height: 100vh;
  width: 100vw;
  background: transparent;
  z-index: 9999999999 !important;
  transition: 0.3s ease;
  pointer-events: none;
  visibility: hidden;
  &.open {
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(0.5px);
    visibility: visible;
    transition: 0.3s ease;
    pointer-events: auto;
    #cart-drawer {
      transform: translateX(0%);
      display: flex;
      flex-direction: column;
    }
  }
  #cart-drawer {
    height: 100%;
    width: 470px;
    background-color: white;
    float: right;
    transition: 0.3s ease;
    transform: translateX(103%);
    pointer-events: auto;
    @include to-sm() {
      max-width: 370px;
    }
    @include to-xs() {
      max-width: 360px;
    }
    @include to-xxs() {
      max-width: 260px;
    }
    > .top {
        background-color: var(--secondary-color);
        padding: 17px 45px 0 10px;
        position: relative;
        .closeSideCart {
            position: absolute;
            top: 50%;
            right: 11px;
            transform: translateY(-50%);
            svg {
                stroke: #fff;
                transition: 0.3s all;
                &:hover {
                    rotate: 90deg;
                }
            }
        }
        .cart-tabs {
            display: flex;
            gap: 7px;
            .cart-tab {
                background-color: var(--primary-color);
                border-radius: 3px 3px 0 0;
                padding: 5px 10px;
                display: flex;
                align-items: center;
                gap: 3px;
                cursor: pointer;
                &.active {
                    background-color: #fff;
                    span {
                        font-weight: 700;
                        text-decoration: underline;
                    }
                }
                &:hover:not(.active) {
                    svg {
                        transform: scale(1.2);
                    }
                }
                svg {
                    width: 17px;
                    height: 17px;
                    transition: 0.3s all;
                    &.heart-full {
                        fill: var(--accent-2-color);
                    }
                }
                span {
                    font-size: 16px;
                    display: inline-flex;
                    flex-direction: column;
                    align-items: center;
                    @include to-sm() {
                        font-size: 12px;
                    }
                    &:after {
                        content: attr(data-title);
                        content: attr(data-title) / "";
                        height: 0;
                        visibility: hidden;
                        overflow: hidden;
                        user-select: none;
                        pointer-events: none;
                        font-weight: 700;
                        @media speech {
                            display: none;
                        }
                    }
                    &:hover {
                        font-weight: 700;
                    }
                }
            }
        }
    }
    .free-delivery-status {
      margin: 16px 22px 0;
      padding: 8px;
      background-color: var(--accent-2-color);
      color: var(--text-primary-color);
      text-align: center;
      font-size: 13px;
      font-weight: 700;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      border-radius: 5px;
    }
    .inner {
      width: 100%;
      display: flex;
      flex: 1;
      flex-direction: column;
      > div {
        height: 100%;
      }
    }
    > form {
      flex: 1;
      display: flex;
      .cart {
        .items-wrapper {
          flex: 1;
          position: relative;
          margin: 20px;
          margin-right: 11px;
          .items {
            position: absolute;
            width: 100%;
            height: 100%;
            overflow-y: auto;
            @include to-md() {
              padding: 0;
            }
            &::-webkit-scrollbar {
              margin-left: 10px;
              width: 4px;
              background: transparent;
            }
            &::-webkit-scrollbar-track {
              background-color: #fff;
            }
            &::-webkit-scrollbar-thumb {
              background-color: var(--accent-1-color);
              border-radius: 2px;
            }
            .additional-items-wrapper {
              border-top: 3px solid #EDEDED;
              margin-right: 10px;
              @include from-sm() {
                border-top: unset;
              }

              // Accordion Title
              .additional-items-title {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 10px 0;

                cursor: pointer;

                @include from-sm() {
                  display: none;
                }

                * { pointer-events: none; }

                p {
                  font-size: 14px;
                  font-weight: normal;
                  margin: 0;

                  display: flex;
                  justify-content: center;
                  align-items: center;
                  gap: 0 10px;
                }
                svg {
                  width: 14px;
                  height: 14px;
                  scale: 1 1;
                  transition: all 0.3s;
                  &.active {
                    transform: rotateX(180deg);
                  }
                }

                &.active {
                  svg { scale: 1 -1; }
                }

                .counter {
                  padding: 3px;
                  line-height: 20px;
                  width: 20px; height: 20px;
                  display: inline-flex; justify-content: center; align-items: center;

                  border-radius: 21px;
                  color: white;
                  font-size: 10px;
                  // background: var( --primary-color );
                  background: var( --text-primary-color );
                }
              }

              // Accordion Content
              .additional-items {
                overflow: hidden;
                max-height: 0;
                &.active { max-height: 100000px; }
                transition: max-height .15s ease-in-out;

                @include from-sm() {
                  max-height: unset;
                  overflow: unset;
                }
                .additional-items-inner {
                  padding: 10px 0;
                }
              }
            }
            .item {
              display: flex;
              margin-right: 10px;
              padding: 16px 0;
              border-bottom: 1px solid var(--primary-color);
              &:first-of-type {
                padding-top: 0;
              }
              &:last-of-type {
                padding-bottom: 0;
                border-bottom: none;
              }
              .img-wrapper {
                display: flex;
                justify-content: center;
                width: 94px;
                height: 94px;
                img {
                  width: 100%;
                  height: 100%;
                  object-fit: contain;
                }
                svg {
                  width: 94px;
                }
              }
              .info-wrapper {
                flex: 1;
                padding-left: 15px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                @include to-sm() {
                  padding-left: 15px;
                }
                .top {
                  & > div {
                    display: flex;
                    justify-content: space-between;
                  }
                  a {
                    margin: 0px;
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 16px;
                    text-transform: capitalize;
                  }
                  svg {
                    cursor: pointer;
                    width: 20px;
                    height: 20px;
                  }
                  span {
                    margin-bottom: 0px;
                    font-size: 12px;
                  }
                }
                ul {
                  margin: 0;
                  padding: 0;
                  list-style: none;
                  * {
                    font-size: 12px;
                  }
                  li {
                    padding: 3px 0;
                    text-transform: capitalize;
                  }
                }
                .mid {
                  font-size: 12px;
                }
                .bottom {
                  display: flex;
                  justify-content: space-between;
                  align-items: flex-end;
                  margin-top: 6px;

                  .price {
                    * {
                      font-size: 16px;
                    }
                    .item-compare-price {
                      text-decoration: line-through;
                    }
                    .item-sale-price, .item-final-price {
                      font-weight: 700;
                    }
                  }
                }
                .quantity-wrapper {
                  .quantity {
                    min-width: 20px;
                    min-height: 30px;
                    font-weight: 600;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: var(--primary-color);
                    border: 1px solid var(--primary-color);
                  }
                  .minus, .plus {
                    min-width: 30px;
                    min-height: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 16px;
                    line-height: 0;
                    color: #000;
                    background-color: var(--primary-color);
                    border: 1px solid var(--primary-color);
                  }
                }
              }
            }
          }

          .empty-cart {
            padding-top: 100px;
            text-align: center;
          }
        }
        .cart-recommendations {
          padding: 10px 0 20px 20px;
          padding-right: 0;
          @include from-sm() {
            display: none;
          }
          .heading {
            margin-bottom: 10px;
          }
          .product-card {
            gap: 1px;
            .product-image {
              .product-badges {
                gap: 2px;
                .product-badge {
                  font-size: 10px;
                  padding: 3px 6px;
                }
              }
            }
            .product-title {
              font-size: 14px;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
            .product-reviews {
              display: none;
            }
            .product-description {
              display: none;
            }
            .product-price {
              span {
                font-size: 14px;
              }
            }
            .product-buttons {
              gap: 0;
              .product-link {
                font-size: 14px;
                padding: 3px 6px;
              }
              button {
                border: none;
                max-height: 26px;
                svg {
                  width: 15px;
                  height: 17px;
                }
              }
            }
          }
        }
        .sub-total-wrapper{
          p:first-of-type {
            font-size: 22px;
          }
        }
      }
    }
    .cart-content {
      display: flex;
      flex-direction: column;
      flex: 1;
      display: none;
      .buttons {
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        .additional-checkout-buttons {
          .shopify-cleanslate {
            div[role="button"] {
              max-height: 40px;
              border-radius: 40px!important;
            }
            ul {
              flex-direction: row!important;
              li {
                margin-top: 0!important;
                margin-right: 4px!important;
                height: unset!important;
                &:nth-of-type(n+3) {
                  display: none;
                }
              }
            }
          }
        }
        .checkout {
          flex: 1;
          padding-top: 13px;
          padding-bottom: 13px;
          text-align: center;
        }
      }
      .content-heading {
        display: flex;
        flex-wrap: wrap;
        gap: 11px;
        align-items: center;
        justify-content: center;
        padding: 20px;
        svg {
          width: 30px;
          height: 30px;
          &.heart-full {
            fill: var(--accent-2-color);
          }
        }
      }
      .grid-container {
        overflow-y: auto;
        position: relative;
        flex: 1;
        padding: 0 20px;
        margin-bottom: 20px;
        margin-right: 8px;
        &::-webkit-scrollbar {
          margin-left: 10px;
          width: 4px;
          background: transparent;
        }
        &::-webkit-scrollbar-track {
          background-color: #fff;
        }
        &::-webkit-scrollbar-thumb {
          background-color: var(--accent-1-color);
          border-radius: 2px;
        }
        .side-cart-saved-grid {
          opacity: 0;
          grid-template-columns: 1fr 1fr;
          position: absolute;
          display: grid;

          grid-gap: 20px;
          width: calc(100% - 29px);
          @include to-sm() {
            grid-template-columns: 1fr;
          }

          & + .loader {
            position: absolute;
            left:50%;
            top: 120px;
          }

          &.loaded {
            opacity: 1;
            transition: 1s;

            & + .loader {
              opacity: 0;
              transition: 0.25s;
            }
          }

          
        }
        .empty-wishlist{
          position: absolute;
          text-align: center;
          width: 100%;
        }

        .empty-wishlist-side-cart {
          display: none;
          text-align: center;
          margin-top: 40px;
          &.active {
            display: block;
            position: relative;
            z-index: 9;
          }
          .closeSideCart {
            width: auto;
            height: auto;
            max-width: fit-content;
            margin: 0 auto;
            margin-top: 20px;
            &:hover {
              cursor: pointer;
              background-color: var(--button-1-color);
              color: #fff;
            }
          }
        }
      }
    }
    span.closeSideCart {
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      svg {
        height: max-content;
        fill: black;
      }
    }
    .cart-details {
      padding: 15px 20px;
      .total-wrapper {
        display: flex;
        justify-content: space-between;
        margin-bottom: 8px;
      }
    }
  }
  .side-cart-recommended {
    border-radius: 5px;
    opacity: 1;
    position: absolute;
    top: 50%;
    margin-left: 4.3vw;
    transform: translate(-120%, -50%);
    padding: 20px 15px;
    padding-bottom: 35px;
    background-color: #fff;
    width: calc(100% - clamp(415px, 25vw + 50px, 504px) - 12vw);
    pointer-events: none;
    max-width: 1300px;
    transition: all 1s ease;
    @include to-width(1134px) {
      width: calc(100% - clamp(415px, 25vw + 50px, 504px) - 4vw);
    }
    @include to-sm() {
      display: none;
    }
    .recommended-heading {
      margin-bottom: 20px;
      .heading-md {
        font-size: 20px;
      }
    }
    .swiper-button-prev {
      left: 2px !important;
      color: #000;
      &:after {
        font-size: 21px;
      }
    }
    .swiper-button-next {
      right: 2px !important;
      color: #000;
      &:after {
        font-size: 21px;
      }
    }
    .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
      color: transparent;
      opacity: 0;
    }
      &.open {
          opacity: 1;
          pointer-events: auto;
          transform: translate(0, -50%);
          min-height: 300px;
      }
  }
}