.product-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  font-size: 12px;
  .product-image {
    position: relative;
    padding-bottom: 100%;
    overflow: hidden;
    &:hover .image-hover {
      opacity: 1;
      transform: scale(1.05);
    }
    img, svg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease;
      &.image-hover {
        opacity: 0;
        transition: 0.5s all;
        transform: scale(1);
      }
    }
    svg {
      width: 100%;
      height: auto;
      &:hover {
        fill: unset;
      }
    }

    .wishlist-heart {
      position: absolute;
      right: 10px;
      top: 10px;
      width: 22px;
      height: 22px;
      svg {
        cursor: pointer;
      }
    }
    .quickview-eye {
      position: absolute;
      right: 10px;
      bottom: 10px;
      width: 30px;
      height: 22px;
      cursor: pointer;
  
      svg {
        height: 21px;
  
        path {
          stroke: #000000;
        }
      }
    }
  }
  .product-title-reviews {
    font-size: 14px;
    text-transform: capitalize;
    .product-title {
      font-weight: bold;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;  
      overflow: hidden;
      line-height: 1.3;
    }
  }
  .product-description {
    font-size: 12px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;  
    overflow: hidden;
    min-height: 30px;
  }
  .product-buttons {
    display: flex;
    gap: 5px;
    text-align: center;
    .product-link {
      flex: 1;
      @include to-xs() {
        padding: 6px 8px;
      }
    }
    .quick-add-cart {
      border-radius: 50%;
      min-height: 35px;
      height: 35px;
      min-width: 35px;
      width: 35px;
      padding: 0 !important;
      background-color: var(--primary-color);
      color: var(--primary-text-color);
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        padding-left: 3px;
      }
    }
  }
}

.product-badges {
    position: absolute; 
    top: 0;  
    left: 0;
    width: 50%;
    padding: 0;  
    pointer-events: none;
    display: flex; 
    flex-direction: column; 
    flex-wrap: wrap; 
    gap: 5px;

  .product-badge {
    font-size: 12px;
    color: white;
    padding: 5px 10px;
    white-space: nowrap; 
    background: #313131;
    position: relative; 
    overflow: hidden;
    width: max-content;

    &.new-arrival-badge { 
      background-color: var(--accent-1-color); 
    }

    &.discount-percentage-badge { 
      background-color: var(--accent-1-color); 
    }

    &.limited-edition-badge {
      background-color: var(--accent-2-color); 
    }

    &.bestseller-badge {
      background-color: var(--primary-color);
    }

    &.low-stock-badge {
        background-color: var(--primary-color);
    }

    &::after {
      content: "";
      display: block; 
      width: 100%;  height: 100%; 
      position: absolute; top: 0; left: 0;
      transform: translateX( -100% );

      opacity: 0.3;
      background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,0.8) 50%,rgba(128,186,232,0) 99%,rgba(125,185,232,0) 100%);
      animation: badge_shine 10s infinite 3s;
    }

    @keyframes badge_shine {
      0% { transform: translateX( -100% ); }
      90% { transform: translateX( -100% ); }
      100% { transform: translateX( 100% ); }
    }
  }
}