.predictive-search-container {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100%;
  z-index: 999999;
  display: flex;
  top: 160px;
  display: none;
  opacity: 0;

  &.active {
    display: flex;
    opacity: 1;
  }

  .predictive-search-modal {
    position: relative;
    transform: translateY(-160px);
    margin: auto;
    background: white;
    min-height: 100px;
    width: clamp(320px, 90vw, 1300px);
    border-radius: 12px;
    box-shadow: 0px 6px 21px 5px rgb(0 0 0 / 38%);

    .inner {
      width: 100%;
      height: 100%;
      min-height: 432px;
      padding: 30px;
      max-height: calc(90vh - 160px);
      overflow: scroll;
      -ms-overflow-style: none;
      scrollbar-width: none; /* Firefox */

      &::-webkit-scrollbar {
        display: none;
      }

      .main-search {
        margin: 30px;
        .page-width {
          padding: 0;
          .result_amount {
            margin-bottom: 30px;
          }
        }
      }
    }

    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      background-color: white;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      cursor: pointer;
      display: flex;
      z-index: 99;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: 100%;
        fill: var(--primary-color);
        margin: auto;
      }
      &:hover {
        rotate: 180deg;
      }
    }
  }

  .predictive-search-results {
    width: 100%;
    // padding: 40px;
    padding: 5px 0px;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    // gap: 15px;

    .resource-header,
    .resource-results {
      width: 100%;
      p {
        margin: 0;
      }
    }

    .resource-header {
      border-bottom: solid 1px #ddd;
      padding: 15px 0px;
      margin-bottom: 5px;

      p {
        font-weight: bold;
        text-transform: capitalize;
        padding: 0 40px;
      }
    }

    .resource-results {
      // padding: 5px 0px 7.5px;
      // padding: 5px 40px 5px 50px;
      padding: 0 5px;

      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      gap: 5px;

      .resource-result {
        width: 100%;
        padding: 5px 5px 5px 50px;

        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row;
        gap: 20px;

        // &:nth-of-type(even) {  background: #eee;  }
        &:hover {
          background: rgb(241, 241, 241);
          // .resource-result-image { transform: scale( 1.25 ); }
        }

        .resource-result-title {
          width: max-content;
          padding: 0px 10px 0px 5px;

          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          flex-direction: row;

          // border-right: solid 1px #707070;
        }

        .resource-result-url {
          white-space: nowrap;
          font-size: 12px;
          color: #707070;
          @media (max-width: 1024px) {
            display: none;
          }
        }

        .resource-result-image {
          width: 30px;
          height: 30px;
          background: white;
          margin: 0 0 0 auto;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          display: none; // Until images are added
        }
      }
    }
  }
}